            this.$set(this.params2, "timeValue3", [
              res.data.data.start_date,
              res.data.data.end_date
            ]);
            this.$set(this.params2, "start_time", res.data.data.start_date);
            this.$set(this.params2, "end_time", res.data.data.end_date);
          }
        });
      }
      this.listShow = false;
    },
    exportOrder(scheduleType, type) {
      // 导出库存收发明细表导出URL
      if (type == "y") {
        let s = Object.assign(this.params, {
          export_type: scheduleType
        });

        this.$axMethod.exportThreePacketsProfitLossUrl(s).then(res => {
          if (res.data.code == 200) {
            window.location.href = res.data.data.url;
          }
        });
      } else {
        let s = Object.assign(this.params2, {
          export_type: scheduleType
        });

        let vm = this;
        this.$axMethod.exportThreePacketsProfitLossUrl(s).then(res => {
          if (res.data.code == 200) {
            window.location.href = res.data.data.url;
            this.$set(this.params2, "period_type", [1]);
            this.$set(this.params2, "timeValue3", [
              vm.params2.start_time,
              vm.params2.end_time
            ]);
            // this.$nextTick(() => {
            //   this.$set(this.params2, "timeValue3", this.params2.timeValue3);
            //   this.$set(this.params2, "start_time", this.params2.start_time);
            //   this.$set(this.params2, "end_time", this.params2.end_time);
            // });
          }
        });
      }
    },
    change1(val) {
      this.params.c_business_time_start = val[0];
      this.params.c_business_time_end = val[1];
    },
    change2(val) {
      this.params.f_business_time_start = val[0];
      this.params.f_business_time_end = val[1];
    },
    change3(val) {
      this.params2.start_time = val[0];
      this.params2.end_time = val[1];
    },
    onReset(type) {
      /** 重置时 如需保存默认参数，则： */

      if (type == 1) {
        this.$set(this, "params", {});
        this.timeValue1 = [];
        this.timeValue2 = [];
        this.$set(this, "searchParams", {
          params: this.params, //1默认带的参数
          TableData: this.TableData, //列表数据
          API: "threePacketsProfitLossList", //接口名
          vm: this //传递的this
        });
        Object.assign(this.searchParams.params, this.params, {
          page: 1
        });
      } else {
        this.$set(this, "params2", {
          time_type: 1,
          period_type: [1],
          company_id: []
        });
        this.$axMethod.getInfoAccountingToTime({}).then(res => {
          if (res.data.code == 200) {
            this.$set(this.params2, "timeValue3", [
              res.data.data.start_date,
              res.data.data.end_date
            ]);
            this.$set(this.params2, "start_time", res.data.data.start_date);
            this.$set(this.params2, "end_time", res.data.data.end_date);
            this.$set(this, "searchParams", {
              params: this.params2, //1默认带的参数
              TableData: this.TableData, //列表数据
              API: "threePacketsProfitLossList", //接口名
              vm: this //传递的this
            });
            Object.assign(this.searchParams.params, this.params2, {
              page: 1
            });
          }
        });
      }
      this.listShow = true;
      getData(this.searchParams, 2);
    },
    getSummaries(param) {
      let _this = this;
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "本页小计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);

          if (
            index === 14 ||
            index === 15 ||
            index === 16 ||
            index === 17 ||
            index === 18 ||
            index === 19 ||
            index === 20 ||
            index === 21 ||
            index === 22 ||
            index === 23 ||
            index === 24 ||
            index === 25 ||
            index === 26 ||
            index === 27 ||
            index === 28 ||
            index === 29
          ) {
            sums[index] = Number(sums[index]).toFixed(2);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    },
    onSearch(formName, type) {
      //查询
      if ((type = 2)) {
        this.timeValue3 = this.params2.timeValue3.length ? "1" : "";
      }
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.listShow = true;
          this.$set(this, "searchParams", {
            params: {}, //1默认带的参数
            TableData: this.TableData, //列表数据
            API: "threePacketsProfitLossList", //接口名
            vm: this //传递的this
          });
          if (formName == "filterFormRef") {
            Object.assign(this.searchParams.params, this.params, {
              page: 1
            });
          } else {
            Object.assign(this.searchParams.params, this.params2, {
              page: 1
            });
          }
          getData(this.searchParams, 2, tableResize.bind(this));
        }
      });
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    }
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
  },
  mounted() {
    const elTable1 = this.$refs.table1.$el.getElementsByClassName(
      "el-table__body-wrapper"
    )[0];
    const elTable2 = this.$refs.table_sum;
    elTable1.addEventListener("scroll", function() {
      elTable2.scrollLeft = this.scrollLeft;
    });
    elTable2.addEventListener("scroll", function() {
      elTable1.scrollLeft = this.scrollLeft;
    });
  }
};
</script>

